import styled from '@emotion/styled';
import { ReactComponent as Num2 } from 'images/sk/event_2/title_num_2.svg';
import { ReactComponent as Together1 } from 'images/sk/event_2/title_together_1.svg';
import { ReactComponent as Together2 } from 'images/sk/event_2/title_together_2.svg';
import { ReactComponent as Together3 } from 'images/sk/event_2/title_together_3.svg';
import HandHeart from 'images/sk/event_2/img_hand_heart.png';

function Event2() {
  const Container = styled.div`
    margin: 0 0 32.7vw 0;
  `;
  const Box = styled.div`
    margin: ${(props) => props.margin || `0`};
  `;
  const HandHeartBox = styled.div`
    margin: 19.8vw 0 17.2vw 0;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 71.6vw;
      max-width: 1032px;
    }
  `;

  return (
    <Container>
      <Box>
        <Num2 width="9.7vw" />
      </Box>
      <Box margin={`6.8vw 0 0 0`}>
        <Together1 width="39.7vw" />
      </Box>
      <Box margin={`2.3vw 0 0 0`}>
        <Together2 width="70.2vw" />
      </Box>
      <HandHeartBox>
        <img src={HandHeart} alt="handheart" />
      </HandHeartBox>
      <Box margin={`17.2vw 0 0 0`}>
        <Together3 width="71.1vw" />
      </Box>
    </Container>
  );
}

export default Event2;
