import styled from '@emotion/styled';
import { useEffect, useState } from 'react';
import BottomSheet from 'components/Friend/BottomSheet';
import FriendLottie from 'components/Friend/FriendLottie';
import Invite from 'components/Friend/Invite';
import Btn from 'components/Friend/Btn';
import Step from 'components/Friend/Step';
import initialize from 'components/Kakao/initialize';
import MyStock from 'components/Friend/MyStock';
import StockList from 'components/Friend/StockList';
import Notice from 'components/Friend/Notice';

function Friend() {
  const [friends, setFriends] = useState(4);
  const [mystock, setMystock] = useState(4);
  const [agreeModal, setAgreeModal] = useState(false);
  const [agreeBottomSheet, setAgreeBottomSheet] = useState(false);
  const [shareBottomSheet, setShareBottomSheet] = useState(false);

  //카카오 공유하기 초기화작업
  useEffect(() => {
    initialize();
  }, []);

  //스크롤 방지
  useEffect(() => {
    if ((!agreeModal && !agreeBottomSheet) || !shareBottomSheet) {
      document.body.style.overflow = 'auto';
    }
    if (agreeModal || agreeBottomSheet || shareBottomSheet) {
      document.body.style.overflow = 'hidden';
    }
  }, [agreeModal, agreeBottomSheet, shareBottomSheet]);
  const Container = styled.div`
    margin-bottom: 21vw;
  `;
  return (
    <>
      {agreeBottomSheet === true ? (
        <BottomSheet
          onClose={() => setAgreeBottomSheet(false)}
          type="agree"
          onClick={() => {
            setShareBottomSheet(true);
            setAgreeBottomSheet(false);
          }}
        />
      ) : null}
      {shareBottomSheet === true ? (
        <BottomSheet type="share" onClose={() => setShareBottomSheet(false)} />
      ) : null}
      <FriendLottie />
      <Step />
      <Invite friends={friends} />
      <MyStock mystock={mystock} />
      <StockList />
      <Notice />
      <Btn
        fixedBtn={true}
        disabledBtn={false}
        onClick={() => setAgreeBottomSheet(true)}
      >
        친구 초대하고 무제한 주식받기
      </Btn>
      {/* <Btn disabledBtn={true} fixedBtn={true}>
        이벤트가 종료되었어요
      </Btn> */}

      <Container />
    </>
  );
}

export default Friend;
