/** @jsxImportSource @emotion/react */

import { useEffect, useState, useMemo } from 'react';
import { throttle } from 'lodash';

import { css } from '@emotion/react';

export default function ScrollBtn({ pointRef }) {
  const HEADER_HEIGHT = 500;
  const [isPoint, setIsPoint] = useState(false);

  const setPointBtn = useMemo(
    () =>
      throttle(() => {
        const isReachPoint =
          window.scrollY > pointRef.current.offsetTop - HEADER_HEIGHT;
        if (isReachPoint !== isPoint) setIsPoint(isReachPoint);
      }, 300),
    [isPoint]
  );

  useEffect(() => {
    window.addEventListener('scroll', setPointBtn);

    return () => {
      window.removeEventListener('scroll', setPointBtn);
    };
  }, [isPoint, setPointBtn, pointRef]);

  return (
    <div css={btnStyle(isPoint)}>
      {isPoint ? (
        <div>
          <button>기부하기</button>
        </div>
      ) : (
        <div>
          <button>이벤트 신청하기</button>
        </div>
      )}
    </div>
  );
}

const btnStyle = (isPoint) => css`
  -webkit-overflow-scrolling: auto;
  /* height: 44px; */
  position: fixed;
  /* position: sticky;
  position: -webkit-sticky;
  position: -moz-sticky;
  position: -o-sticky;
  position: -ms-sticky; */
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: ${isPoint ? '#ffb600' : '#e0e0e0'}; */
  width: 100vw;
  height: 0;
  top: 100vh;
  background-color: #ffffff;

  div {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 4vw;
    background-color: #ffffff;
    background: 'rgba(255, 255, 255, 0.6)';
    display: 'flex';
    justify-content: center;
    align-items: center;
  }
  button {
    background-color: #fe5000;
    width: 100%;
    border-radius: 0 0 0 10px;
    border-radius: 2vw;
    border: none;
    font-size: 4vw;
    height: 13vw;
    font-weight: 700;
    color: #ffffff;
    &:hover {
      background-color: #cc4400;
    }
  }
`;
