import styled from '@emotion/styled';
import React, { useState } from 'react';
import { ReactComponent as TopBtn } from 'images/friend/account/topBtn.svg';
import { ReactComponent as DownBtn } from 'images/friend/account/downBtn.svg';
// 주식 로고
import { ReactComponent as Hyundea } from 'images/sk/logo/logo_hyundea.svg';
import { ReactComponent as Gs } from 'images/sk/logo/logo_gs.svg';
import { ReactComponent as Sk } from 'images/sk/logo/logo_sk.svg';
import { ReactComponent as Winia } from 'images/sk/logo/logo_winia.svg';
import { ReactComponent as Jyp } from 'images/sk/logo/logo_jyp.svg';
import { ReactComponent as Jeju } from 'images/sk/logo/logo_jeju.svg';
import { ReactComponent as Hite } from 'images/sk/logo/logo_hite.svg';
import { ReactComponent as Tway } from 'images/sk/logo/logo_tway.svg';
import { ReactComponent as Koreanair } from 'images/sk/logo/logo_koreanair.svg';
import { ReactComponent as Hansol } from 'images/sk/logo/logo_hansol.svg';
import { ReactComponent as Kyochon } from 'images/sk/logo/logo_kyochon.svg';
import { ReactComponent as Maniker } from 'images/sk/logo/logo_maniker.svg';
import { ReactComponent as Tj } from 'images/sk/logo/logo_tj.svg';
const SkStockList = () => {
  const [listShow, setListShow] = useState(false);
  const TitleBox = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0vw 4vw;
    div {
      cursor: pointer;
    }
    h5 {
      font-weight: 700;
      font-size: 4vw;
    }
  `;

  const CompanyBox = styled.div`
    margin: ${(props) => props.margin};
    display: flex;
    flex-direction: column;
    gap: 2 vw;
    div {
      width: auto;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 2vw;
      div {
        display: flex;
        align-items: center;
        gap: 2vw;
      }
      p {
        margin: 0;
        font-weight: 700;
        color: #3d3d3d;
        font-size: 3.7vw;
      }
    }
  `;

  const Line = styled.div`
    height: 1vw;
    background-color: #f5f5f5;
  `;

  return (
    <>
      <Line margin={`0 0 0 0`}></Line>
      <TitleBox>
        <h5>어떤 주식을 받을 수 있나요?</h5>
        <div>
          {listShow ? (
            <TopBtn width="4vw" onClick={() => setListShow(false)} />
          ) : (
            <DownBtn width="4vw" onClick={() => setListShow(true)} />
          )}
        </div>
      </TitleBox>
      {listShow ? (
        <>
          <CompanyBox margin={`0 4vw 3vw 4vw`}>
            <div>
              <div>
                <div>
                  <Hyundea width="20px" />
                </div>
                <p>현대차</p>
              </div>
              <div>
                <div>
                  <Gs width="20px" />
                </div>
                <p>GS글로벌</p>
              </div>
              <div>
                <div>
                  <Sk width="20px" />
                </div>
                <p>SK하이닉스</p>
              </div>
              <div>
                <div>
                  <Winia width="20px" />
                </div>
                <p>위니아</p>
              </div>
              <div>
                <div>
                  <Jyp width="20px" />
                </div>
                <p>JYP Ent.</p>
              </div>
              <div>
                <div>
                  <Jeju width="20px" />
                </div>
                <p>제주맥주</p>
              </div>
              <div>
                <div>
                  <Hite width="20px" />
                </div>
                <p>하이트진로</p>
              </div>
              <div>
                <div>
                  <Tway width="20px" />
                </div>
                <p>티웨이항공</p>
              </div>
              <div>
                <div>
                  <Koreanair width="20px" />
                </div>
                <p>대한항공</p>
              </div>
              <div>
                <div>
                  <Hansol width="20px" />
                </div>
                <p>한솔홈데코</p>
              </div>
              <div>
                <div>
                  <Kyochon width="20px" />
                </div>
                <p>교촌에프앤비</p>
              </div>
              <div>
                <div>
                  <Maniker width="20px" />
                </div>
                <p>마니커</p>
              </div>
              <div>
                <div>
                  <Tj width="20px" />
                </div>
                <p>TJ미디어</p>
              </div>
            </div>
          </CompanyBox>
        </>
      ) : (
        <Line margin={`0 0 0 0`}></Line>
      )}
    </>
  );
};

export default SkStockList;
