import styled from '@emotion/styled';
import React from 'react';
const SkNotice = () => {
  const TitleBox = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 14.9vw;
    h5 {
      margin: 0 0 0 4vw;
      font-weight: 700;
      font-size: 4.2vw;
    }
  `;

  const DetailBox = styled.div`
    background-color: #f5f5f5;
    padding: 4vw 4vw 12vw 4vw;
    p {
      text-align: start;
      color: #8c8c8c;
      font-size: 3.8vw;
      font-weight: 400;
      margin: 0;
      line-height: 6vw;
    }
  `;

  return (
    <>
      <TitleBox>
        <h5>유의사항</h5>
      </TitleBox>
      <DetailBox>
        <p>
          - 본 이벤트는 1회만 참여가 가능하며, 당첨 경품 지급은 1인 1계좌에 한해
          지급됩니다. <br />
          - 본 이벤트는 당사 사정 또는 금융당국 지도나 권고로 조기 종료될 수
          있습니다. <br />
          - 커피하우스 앱 내에서 SK증권 신규 계좌 개설 후 연동하거나 기존 SK증권
          계좌를 연동한 고객에 한해서 이벤트 참여가 가능합니다. <br />
          - 부정확한 개인정보로 인해 경품을 수령하지 못하는 경우, 당첨이
          취소(제외)될 수 있습니다. <br />
          - 본 이벤트는 참여(응모) 후 서비스에서 탈퇴/재가입하는 경우 당첨자에서
          제외되며 다시 참여할 수 없습니다. <br />
          - 본 이벤트는 경품 당첨 이후 서비스에서 탈퇴/재가입하는 경우
          경품(혜택)이 지급되지 않습니다. <br />
          - 이벤트 당첨자 선정 및 발표, 알림을 목적으로 개인정보(①성함 ②연락처
          ③증권계좌번호 ④커피하우스 ID)를 수집·이용합니다. <br />
          - 아래와 같은 사유 발생 시 경품 지급 대상에서 제외됩니다. <br />
          ① 가입 정보가 허위이거나, 자전거래나 해킹 같은 부정한 방법을 사용한
          경우 <br />
          ② (제세공과금 세금 신고가 필요한 경우) 세금 신고와 주식(경품) 지급에
          필요한 개인정보(주민번호13자리)를 제출하지 않거나 거부한 경우 <br />
          ③ 경품 지급 시점 서비스에서 탈퇴되어 있는 회원의 경우 <br />
          ④ 개설한 계좌가 사고 계좌(출금 금지 계좌)나 지급정지 계좌로 등록된
          경우 <br />
          (비대면 계좌 개설 후 20영업일 이내 다른 비대면 계좌 개설 시 사고
          계좌로 등록될 수 있습니다.) <br />
          <br />
          - 본 이벤트는 경품 제공을 위해 대행업체((주)티아이커머스)에 고객님의
          정보를 제공합니다. <br />
          - 경품(혜택)가액 체결 기준 5만원 초과 경품 수령 시 대행업체
          ((주))티아이커머스)에서 고객님의 주민등록번호 13자리를 제세공과금(22%)
          신고를 위해 수집·이용합니다. <br />
          - 경품(혜택)가액 체결 기준 5만원 초과 경품 수령 시 제세공과금(22%)는
          소셜인베스팅랩에서 부담합니다. 단, 해당 소득은 기타소득에 포함되어
          연간 합계 300만원 초과 시 종합과세에 포함됩니다. <br />
          - 주식(경품) 지급 시기는 이벤트 신청일로부터 20영업일 이내이며, 이벤트
          참여 시 연동한 계좌로 자동 입고 처리됩니다. <br />
          - 본 이벤트는 당사의 사정에 의해 예고없이 변경되거나 중단될 수
          있습니다. <br />
          - 주식(경품)혜택은 당사 이벤트 종목선정 기준에 따라 선정되었으며, 당사
          사정에 의해 지급 종목이 변경될 수 있습니다. <br />
          - 주식(경품)가액은 매수 시점의 체결가를 기준으로 하기에, 금액이 상이할
          수 있습니다. <br />
          - 당첨된 종목이 거래정지 등의 사유로 지급이 불가할 경우 해당 주식의
          거래정지 전일 종가에 상응하는 주식으로 대체 지급합니다. <br />
          - 당첨 후 주식 입고 전에 계좌 관리점 변경, 통합/폐쇄되는 경우, 계좌
          재개설을 하더라도 당첨 주식은 지급되지 않습니다. <br />
        </p>
      </DetailBox>
    </>
  );
};

export default SkNotice;
