import styled from '@emotion/styled';
import { ReactComponent as FriendEvent } from 'images/sk/event_2/img_friend_event.svg';
import { ReactComponent as TitleFriend1 } from 'images/sk/event_2/title_friend_event_1.svg';
import { ReactComponent as Together2 } from 'images/sk/event_2/title_together_2.svg';
import { ReactComponent as Together3 } from 'images/sk/event_2/title_together_3.svg';
import HandHeart from 'images/sk/event_2/img_hand_heart.png';
import SkBtn from './SkBtn';
import SkNotice from './SkNotice';

function Event3() {
  const Container = styled.div`
    margin: 0 0 20vw 0;
  `;
  const Box = styled.div`
    margin: ${(props) => props.margin || `0`};
  `;

  const Line = styled.div`
    height: 1vw;
    background-color: #f5f5f5;
  `;

  const DetailBox = styled.div`
    padding: 4.8vw 4.2vw 4.2vw 4.2vw;
    /* height: 23.4vw; */
    height: auto;
    text-align: start;
    color: #757575;
    font-size: 3.2vw;
    font-weight: 400;
    line-height: 4.8vw;
  `;
  return (
    <Container>
      <Box>
        <FriendEvent width="100vw" />
      </Box>
      <Box margin={`11.5vw 0 0 0`}>
        <TitleFriend1 width="66.6vw" />
      </Box>
      <Box margin={`5.4vw 0 8.8vw 0`}>
        <SkBtn>친구 초대하고 주식 받기</SkBtn>
      </Box>
      <Line margin={`0 0 0 0`}></Line>
      <DetailBox>
        <b>이벤트 당첨 시, 개인(신용)정보 수집이용제공 동의 필수</b>
        <br />
        <b>비대면 계좌 개설 또는 계좌 연동 진행 필수</b>
        <br />
        (단, 커피하우스 계좌를 이미 보유하고 있는 경우는 바로 이벤트 참여 가능)
      </DetailBox>
      <SkNotice />
    </Container>
  );
}

export default Event3;
