import styled from '@emotion/styled';
import SkLottie from 'components/Sk/SkLottie';
import { ReactComponent as TopMain } from 'images/sk/detail/img_top_mian_1.svg';
import { ReactComponent as TopMain2 } from 'images/sk/detail/img_top_mian_2.svg';
import { ReactComponent as Num1 } from 'images/sk/event_1/title_num_1.svg';
import { ReactComponent as Free1 } from 'images/sk/event_1/title_free_1.svg';
import { ReactComponent as Free2 } from 'images/sk/event_1/title_free_2.svg';
import { ReactComponent as Free3 } from 'images/sk/event_1/title_free_3.svg';
import { ReactComponent as Commission1 } from 'images/sk/event_1/title_commission_1.svg';
import { ReactComponent as Commission2 } from 'images/sk/event_1/title_commission_2.svg';
import { ReactComponent as Commission3 } from 'images/sk/event_1/title_commission_3.svg';
import { ReactComponent as Commission4 } from 'images/sk/event_1/title_commission_4.svg';
import { ReactComponent as Step } from 'images/sk/event_1/title_step.svg';
import { ReactComponent as Step1 } from 'images/sk/event_1/img_step_1.svg';
import { ReactComponent as Step2 } from 'images/sk/event_1/img_step_2.svg';
import { ReactComponent as Step3 } from 'images/sk/event_1/img_step_3.svg';
import { ReactComponent as Read } from 'images/sk/event_1/title_read.svg';
import Money from 'images/sk/event_1/img_money.png';
import StockListLottie from 'components/Sk/StockListLottie';
import SkStockList from 'components/Sk/SkStockList';
function Event1() {
  const Container = styled.div`
    padding: 0 0 28.3vw 0;
  `;
  const Box = styled.div`
    margin: ${(props) => props.margin || `0`};
  `;
  const LottieBox = styled.div`
    margin: ${(props) => props.margin || `0`};
    z-index: -10;
  `;
  const MoneyImg = styled.img`
    margin: 10.2vw 0 0 0;
    width: 100vw;
    max-width: 1440px;
  `;
  const StepBox = styled.div`
    display: flex;
    justify-content: center;
    margin: 5.56vw 0 0 0;
    align-items: center;
    height: 46.67vw;
    div {
      padding-top: 3.3vw;
      padding-bottom: 6.6vw;
      height: 37.8vw;
    }
    div:not(:last-child) {
      border-right: 1px solid #7c492c;
    }
  `;
  const ReadBox = styled.div`
    margin: 24.7vw 0 10.8vw 0;
    display: flex;
    justify-content: center;
    div {
      border: 1px solid #a1a1a1;
      width: 90.83vw;
      height: 31.9vw;
      display: flex;
      padding-left: 2.7vw;
      border-radius: 3.8vw;
    }
  `;
  return (
    <Container>
      <TopMain width="100vw" />
      <LottieBox margin={`-4px 0 0 0`}>
        <SkLottie />
      </LottieBox>
      <Box>
        <TopMain2 width="100vw" />
      </Box>
      <Box margin={`17.8vw 0 0 0`}>
        <Num1 width="9.7vw" />
      </Box>
      <Box margin={`4vw 0 0 0`}>
        <Free1 width="24.1vw" />
      </Box>
      <Box margin={`4vw 0 0 0`}>
        <Free2 width="60.2vw" />
      </Box>
      <Box>
        <StockListLottie />
      </Box>
      <Box>
        <Free3 width="67.2vw" />
      </Box>
      <Box margin={`38vw 0 0 0`}>
        <Commission1 width="39.4vw" />
      </Box>
      <Box margin={`5.8vw 0 0 0`}>
        <Commission2 width="40.8vw" />
      </Box>
      <Box margin={`2.3vw 0 0 0`}>
        <Commission3 width="70vw" />
      </Box>
      <Box margin={`5.1vw 0 0 0`}>
        <Commission4 width="53vw" />
      </Box>

      <MoneyImg src={Money} alt="money" />
      <Box margin={`19.2vw 0 0 0`}>
        <Step width="26.1vw" />
      </Box>

      <StepBox margin={`5.5vw 0 0 0`}>
        <div>
          <Step1 width="32.7vw" />
        </div>
        <div>
          <Step2 width="32.7vw" />
        </div>
        <div>
          <Step3 width="32.7vw" />
        </div>
      </StepBox>

      <ReadBox>
        <div>
          <Read width="80.2vw" />
        </div>
      </ReadBox>
      <SkStockList />
    </Container>
  );
}

export default Event1;
